@media (max-width: 768px) {
  .welcome-banner {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin: 12px;
    margin-top: 70px;
    padding: 12px;
  }

  .welcome-left h2 {
    font-size: 1.2rem;
  }

  .welcome-left p {
    font-size: 0.9rem;
  }

  .avatar {
    width: 44px;
    height: 44px;
    font-size: 1.1rem;
  }
}
.welcome-banner {
    margin: 20px auto;
    max-width: 1249px;
    padding: 9px 15px;
    border-radius: 14px;
    background: linear-gradient(135deg, rgba(30, 58, 138, 0.9), rgba(15, 23, 42, 0.95));
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: -8px;
    margin-top: 89px;
}

.welcome-left h2 {
    color: #e0f2fe;
    font-size: 1.5rem;
    font-weight: 600;
}

.welcome-left h2 span {
    color: #93c5fd;
}

.welcome-left p {
    margin-top: 6px;
    font-size: 0.95rem;
    color: #cbd5e1;
}

.avatar {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background: linear-gradient(135deg, #60a5fa, #2563eb);
    color: white;
    font-size: 1.5rem;
    font-weight: bold;

    display: flex;
    align-items: center;
    justify-content: center;

    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.35);
}